* {
  box-sizing: border-box;
}
.app-wrapper {
  display: grid;
  grid-template-areas: "header header" "nav content";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  color: black;
}

.app-wrapper__content {
  grid-area: content;
  background-color: mediumaquamarine;
}
